/**
 * 获取某段时间内的报表数据
 *
 * @export
 * @param {string} [id=''] 报表ID
 * @param {string} [createTimeStart=''] 查询开始时间
 * @param {string} [createTimeEnd=''] 查询结束时间
 * @returns promise
 */
export function getReportData(id = '', createTimeStart = '', createTimeEnd = '') {
  return request({
    method: 'post',
    url: '/rc-analyse/query/productreport',
    data: {
      id,
      createTimeStart,
      createTimeEnd
    }
  });
}

