/**
 * 新增或修改变量监控配置信息
 *
 * @export
 * @param {*} id 为空新增，有值更新
 * @param {string} [modulename=''] 样本名称
 * @param {string} [variablekey=''] 变量key
 * @param {*} [settingData=[]] 阀值设置
 * @param {*} status 状态 ：启用 ：Enable，禁用：Disable
 * @returns promise
 */
export function setVarMonitorOptions(id, modulename = '', variablekey = '', settingData = [], status) {
  return request({
    method: 'post',
    url: '/rc-analyse/save/vaiablesetting',
    data: {
      id,
      modulename,
      variablekey,
      settingData,
      status
    }
  });
}

