/**
 * 获取决策拒绝原因监控列表
 *
 * @export
 * @param {*} moduleName 模型名称
 * @param {*} createTimeStart 决策开始时间
 * @param {*} createTimeEnd 决策截至时间
 * @returns
 */
export function getRcrefuseList(moduleName, createTimeStart, createTimeEnd, filterConditionList = []) {
  if (!moduleName || !createTimeStart || !createTimeEnd) {
    return Promise.reject('参数不全');
  }
  return request({
    method: 'post',
    url: '/rc-analyse/query/monitor/rcrefuse',
    data: {
      moduleName,
      createTimeStart,
      createTimeEnd,
      filterConditionList
    }
  });
}

export function getMonitorCenList(productName, monitorType, moduleType, page = 1, size = 10) {
  return request({
    method: 'post',
    url: '/rc-analyse/query/monitoringcenter',
    data: {
      productName,
      monitorType,
      moduleType
    },
    params: {
      page,
      size
    }
  });
}

