<template>
  <div class="monitor-view">
    <el-card>
      <div slot="header">
        <span class="card-name">监控消息中心</span>
      </div>
      <el-row :gutter="20">
        <el-col :span="9">
          <span class="title">产品名称：</span>
          <span>{{monitorData.productName}}</span>
        </el-col>
        <el-col :span="9">
          <span class="title">报表名称：</span>
          <span>{{monitorData.productName}}</span>
        </el-col>
        <el-col :span="6">
          <span class="title">预警等级：</span>
          <span>{{monitorData.warninglevel}}</span>
        </el-col>
      </el-row>
      <div class="el-row">
        <span class="title">变量异常说明：</span>
        <span>{{monitorData.abnormalDes}}</span>
      </div>
      <div class="el-row">
        <span class="title">时间：</span>
        <span>{{monitorData.createTimeStart}}</span>
      </div>
      <div class="el-row">
        <span class="title">处理描述：</span>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 6}"
          placeholder="请输入内容"
          v-model="handingmessage"
        ></el-input>
      </div>
      <div class="el-row">
        <el-button type="primary" @click.prevent.stop="saveFunc">完成</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import {queryMessageDetail, updateMessageDetail} from '@/api/monitor.js';
export default {
  name: 'monitor-view',
  props: ['id'],
  data() {
    return {
      monitorData: {
        productName: '',
        productReportName: '',
        warninglevel: '',
        abnormalDes: '',
        createTimeStart: '',
        desc: ''
      },
      handingmessage: ''
    };
  },
  created() {
    console.log(this.$route.params.id);
    const id = this.$route.params.id;
    queryMessageDetail(id)
      .then(result => {
        if (result.result) {
          Object.assign(this.monitorData, result.result);
          this.handingmessage = this.monitorData.handingmessage;
        } else {
          this.$message.warning('查询失败');
        }
      })
      .catch(err => console.log(err));
  },
  methods: {
    saveFunc() {
      if (!this.handingmessage) {
        this.$message.warning('请填写处理信息');
        return;
      }
      updateMessageDetail(this.monitorData.id, this.handingmessage, this.monitorData.productReportName)
        .then(result => {
          this.$message.success('保存成功');
          this.$router.push('/monitor-list');
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-card {
  margin: 20px 15px;
}
.el-row {
  margin-bottom: 15px;
  min-height: 34px;
}
.el-textarea {
  margin-top: 10px;
}
</style>
